@import "css3";

$searchH: 36px;
$searchBorderRadius: 15px;

.logo {
    float: left;
}

.search-container {
    position: relative;
    overflow: hidden;
    padding-left: 15px;
    img {
        width: 100%;
        display: block;
    }
    & > div {
        position: absolute;
        left: 15px;
        right: 0;
        bottom: 0;
        height: $searchH;
        background-color: #282828;
        padding: 5px 20px;
        @include box-sizing(border-box);
    }
}

.search {
    float: left;
    width: 82%;
    height: 100%;
    position: relative;
    @include box-sizing(border-box);
    input {
        display: block;
        @include box-sizing(border-box);
        width: 100%;
        height: 100%;
        padding: 1px 85px 1px 15px;
        border-radius: $searchBorderRadius;
        border: 1px solid #9e9e9e;
    }
    a {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        background-color: #e5464e;
        color: #fff;
        border: 1px solid #e5464e;
        border-top-right-radius: $searchBorderRadius;
        border-bottom-right-radius: $searchBorderRadius;
        padding: 0 20px;
        line-height: $searchH - 12px;
        @include box-sizing(border-box);
    }
}

.upload-file {
    float: right;
    @include box-sizing(border-box);
    height: 100%;
    width: 15%;
    border: 2px solid #fff;
    border-radius: $searchBorderRadius;
    color: #fff;
    text-align: center;
    line-height: $searchH - 14px;
    &:hover {
        background-color: #fff;
        color: #282828;
    }
}