@import "reset";
@import "css3";
@import "base";
@import "header_bar";

.report-preview {
    width: 750px;
    display: inline-block;
}

.report-thumb-container {
    background-color: #fff;
    & > img {
        width: 104px;
        float: left;
    }
    & > div {
        overflow: hidden;
        padding: 12px 0 0 30px;
        h2 {
            font-size: 20px;
            font-weight: bold;
            margin-bottom: 10px;
        }
        p.report-thumb-info {
            max-width: 50%;
            overflow: hidden;
            -ms-text-overflow: ellipsis;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 10px;
        }
        p.download-count {
            font-size: 13px;
            color: #a8a8a8;
        }
    }
}

.report-ifr-container {
    width: 750px;
    height: 650px;
    overflow: hidden;
    background-color: #fff;
    iframe {
        width: 100%;
    }
}

.report-download-bar {
    height: 45px;
    background-color: #282828;
}

.report-related {
    float: right;
    width: 200px;
    h2 {
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 25px;
        text-align: center;
    }
}

.report-related-list {
    li {
        margin-bottom: 10px;
        & > img {
            width: 48px;
            float: left;
        }
        & > div {
            overflow: hidden;
            padding-left: 10px;
            h3 {
                overflow: hidden;
                -ms-text-overflow: ellipsis;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 14px;
                font-weight: bold;
                margin-bottom: 5px;
            }
            p {
                font-size: 12px;
                color: #909090;
            }
        }
        &::after {
            content: '';
            display: table;
            clear: both;
        }
    }
}