$commonFont: STHeiTi, "Microsoft Yahei", "Helvetica Neue", Helvetica, sans-serif;

body {
    background-color: #f2f2f2;
    font-family: $commonFont;
    font-size: 15px;
    color: #282828;
}

.container {
    width: 960px;
    margin: 0 auto;
    padding: 20px;

    & > section {
        margin-top: 20px;
    }
}

.clearfix {
    &::after {
        content: '';
        display: table;
        clear: both;
    }
}

.hide {
    display: none;
}